.text {
	margin-bottom: 22px;
	font-size: $fontSize16;
	line-height: 28px;
	color: $grayDarker;

	.date {
		padding: 0;
		margin: 0 0 24px 0;
	}

	h1,
	h2,
	h3,
	h4,
	h5,
	h6 {
		font-weight: 500;
		color: $grayDarker;
		line-height: 1.2;
		margin: 0 0 17px;
	}

	h1 {
		font-size: 32px;

		//768px
		@media (min-width: $screen_M_Min) {
			font-size: $fontSizeH1;
		}
	}

	h2 {
		font-size: 30px;

		//768px
		@media (min-width: $screen_M_Min) {
			font-size: $fontSizeH2;
		}
	}

	h3 {
		font-size: 24px;

		//768px
		@media (min-width: $screen_M_Min) {
			font-size: $fontSizeH3;
		}
	}

	h4 {
		font-size: 22px;

		//768px
		@media (min-width: $screen_M_Min) {
			font-size: $fontSizeH4;
		}
	}

	h5 {
		font-size: 20px;

		//768px
		@media (min-width: $screen_M_Min) {
			font-size: $fontSizeH5;
		}
	}

	h6 {
		font-size: 18px;

		//768px
		@media (min-width: $screen_M_Min) {
			font-size: $fontSizeH6;
		}
	}

	iframe {
		margin: 1.5rem 0;
	}

	iframe {

		@media (max-width: 600px) {
			width: 100%;
		}
	}

	hr {
		height: 1px;
		width: 90px;
		background: $grayDark;
		border: 0;
		margin: 0 0 30px 0;
	}

	b,
	strong {
		//font-size: 28px;
		//font-family: $fontFamilyBase;
		color: $grayDark;
	}

	//*SS
	p,
	span {
		line-height: 24px;
		font-size: $fontSize16;
		color: $grayDark;
		margin: 0 0 14px 0;

		//768px
		@media (min-width: $screen_M_Min) {
			//font-size: $fontSize16;
		}
	}

	s {}

	del {}

	pre {}

	a {
		text-decoration: none;

		&:hover {
			text-decoration: underline;
		}
	}


	//http://htmlbook.ru/css/quotes

	q {
		font-style: italic;
	}

	blockquote {
		//background-color: $base;
		border-left: 3px solid #40AC60;

		//font-family: $fontFamilyBase;
		//line-height: 1.3;
		text-align: left;
		//quotes: "\201c" "\201e";
		//text-transform: uppercase;
		position: relative;
		margin: 14px 0 !important;
		padding-left: 15px;
		color: $grayLight;
		font-size: $fontSize16;
		line-height: 23px;

		&:before {
			//content: open-quote;
			//content: "\e901";
			content: '';
			left: -1px;
			top: 5px;
			position: absolute;
			//color: $grayLight;
			//font-size: 14px;
			//background: url(../images/container/quotes.svg) 0 0 no-repeat;
			width: 40px;
			height: 35px;
			//font-family: 'icomoon' !important;
			//border: 3px solid $brandPrimary;
			//height: 100%;
		}

		/*
		&:after {
			//content: close-quote;
			content: "\e900";
			right: -50px;
			bottom: 0;
			//position: absolute;
			color: $grayLight;
			font-size: 18px;
			//font-family: $icomoon;
		}*/

		* {
			//font-family: $fontFamilyBase !important;
			color: $grayDarker !important;
			//font-size: 24px !important;
			display: inline;
			margin: 0;
		}
	}

	img {
		//float: left;
		margin: 10px auto 37px;
		max-width: 100%;
		display: block;
	}

	table {
		width: 100%;
		margin: 0 0 30px;
		//border-collapse: collapse;
		border: 1px solid $primaryColor;

		tr {
			font-size: $fontSize16;
			//font-family: $fontFamilyBase;
			color: $grayDarker;
			text-align: center;
			position: relative;
			padding: 8px 0;
			//line-height: 2.045;
			//border: 1px solid #fff;

			@media (max-width: 440px) {
				//line-height: 1.3;
			}

			&:nth-of-type(odd) {
				background-color: rgba(120, 0, 255, .1);
				//background-clip: padding-box;
				//padding: 1px;
			}

			&:nth-of-type(1) {
				//background-color: #EEEEEE;
				//background-color: $brandPrimary;
				//font-family: $fontFamilyBase;
				color: $primaryColor;
				font-size: 16px;
				//background-clip: padding-box;
				//padding: 1px;

				td {
					padding-top: 9px;
					padding-bottom: 9px;
				}
			}

			&:last-of-type {

				td {
					//border: 0;
					// padding: 0;
					//border-bottom: 1px solid $brandPrimary;
				}

				//&:after {
				//content: '';
				//border-bottom: 1px solid $brandPrimary;
				//display: block;
				//width: 100%;
				//position: absolute;
				//bottom: 0;
				//left: 0;
				//}
			}

			td {
				//border: 1px solid #fff;
				//border-top: 0;
				//border-right: 0;
				//border: 0;
				padding: 8px 6px;
				//border: 1px solid #fff;

				&:nth-of-type(1) {
					text-align: left;
					padding-left: 20px;
				}

				* {
					display: inline;
					//font-size: 14px;
					//font-family: $fontFamilyBase;
					color: $grayLight;
					text-align: center;
					position: relative;
					//line-height: 3.045;
					padding: 8px 0;
					margin: 0;
				}
			}
		}
	}

	ul {
		//list-style: none;
		padding: 0;
		margin: 0 0 28px;
		//list-style-type: square;
		list-style: none url('../../images/squareb.svg') inside;

		li {
			color: $grayLight;
			position: relative;
			//line-height: 28px;
			//padding-left: 17px;
			margin-bottom: 7px;


			/*&:before {
				counter-increment: my;
				content: '' !important;
				//background: url('../images/content/square.png') no-repeat;
				display: block !important;
				//width: 6px;
				//height: 6px;
				//top: 50% !important;
				top: 9px !important;
				//transform: translateY(-50%);
				left: 0 !important;
				color: $brandPrimary !important;
				margin: 0 !important;
				position: absolute;
				width: 6px;
				height: 6px;
				background: $brandPrimary;
			} */
		}
	}

	ol {
		list-style-type: decimal;
		padding: 0;
		margin: 0 0 28px;
		padding-left: 15px;
		color: $primaryColor;

		li {
			font-size: $fontSize16;
			//font-family: $fontFamilyBase;
			color: $grayLight;
			position: relative;
			margin-bottom: 10px;
		}
	}

	//   ()    ol
	//https://html5.by/blog/ol-numbers-css-counter-styling/
	//https://codepen.io/messer/pen/AlDpb
	ol {
		counter-reset: list1;
		/*   */

		ol {
			counter-reset: list2;

			li {

				&:before {
					counter-increment: list2;
					content: '';
				}
			}
		}

		li {
			list-style: none;
			/*      */

			&:before {
				counter-increment: list1;
				/*    */
				content: counter(list1)".";
				/*   */
				//content: counter(list1) "." counter(list2) ". "; /*   */
				color: $primaryColor;
				//background: $brandPrimary;
				display: inline-block;
				text-align: center;
				margin: 5px 10px;
				//line-height: 40px;
				//width: 40px;
				//height: 40px;
			}
		}
	}
}
