/* Reset */
* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
  //outline: none;

  &:before, &:after {
    box-sizing: border-box;
  }
}

img {
  max-width: 100%;
  object-fit: contain;
}

ul, ol {
  list-style: none;
}

button {
  border: none;
  background: none 0 0;
  font: inherit;
  cursor: pointer;
  color: inherit;
  -webkit-appearance: none;
  -moz-appearance: none;
  appearance: none;
  outline: none;
}

input, textarea, select {
  -webkit-appearance: none;
  -moz-appearance: none;
  appearance: none;
  background: none 0 0;
  border-radius: 0;
  outline: none;
}